.cardreport {
    min-width: 570px;
    max-height: 285px;
    min-height: 280px;
    background: #1C1C21;
    border-radius: 25px;
    display: flex;
    flex-direction: row;
    padding: 20px;
    justify-content: space-between;
    margin: 0;
    position: relative;
}

@media screen and (max-width: 440px) {
    .cardreport {
        width: calc(100% - 2*20px);
        display: flex;
        flex-direction: column;
        padding: 5px;
        min-height: 440px;
        margin: 20px auto;
        align-items: center;
        justify-content: flex-start;
    }
}

.title {
    padding: 0;
    margin: 0;
    position: absolute;
    height: 40px;
    left: 0%;
    right: 0%;
    top: 0px;
    background: #2D2C32;
    border-radius: 25px 25px 0px 0px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    color: #A7A7A7;
    display: flex;
    align-items: center;
}

.icon {
    margin: 0 20px 0 30px;
}

.data {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 45px;
    margin: 20px auto;
    gap: 10px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 16px;
    color: #8A8A8C;
}

@media screen and (max-width: 440px) {
    .data {
        margin: 45px auto;
    }
}

.prevday {
    cursor: pointer;
    background: #1C1C21;
    border: none;
    display: flex;
    justify-content: center;
}

.nextday {
    cursor: pointer;
    background: #1C1C21;
    border: none;
    display: flex;
    justify-content: center;
    transform: matrix(-1, 0, 0, 1, 0, 0);
}

.next:hover * {
    fill: #A59557;
}